*, *:before, *:after {
  box-sizing: border-box; }

body {
  font-size: 1rem;
  font-family: Courier New, Courier, monospace;
  background-color: #fff;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

ol,
ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

p {
  font-size: 16px;
  line-height: 24px;
  margin: 0; }

a {
  color: #000; }
  a:hover {
    font-weight: bold;
    text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  margin: 0; }

.site-header {
  text-align: center;
  margin-bottom: 40px; }

.site-brand {
  padding-top: 24px;
  padding-bottom: 24px; }

.site-title-nav {
  padding-top: 24px;
  padding-bottom: 24px;
  padding-left: 4px;
  padding-right: 4px;
  background-color: #fff;
  border-top: 4px solid #000;
  border-bottom: 1px solid transparent; }
  @media (min-width: 600px) {
    .site-title-nav {
      padding-top: 32px;
      padding-bottom: 32px; } }
  .site-title-nav.headroom {
    transition: transform .2s, border-color .2s;
    z-index: 10; }
  .site-title-nav.headroom--not-top {
    position: fixed;
    top: 0;
    width: 100%;
    border-bottom-color: #bfbdbd; }
    @media (max-width: 599px) {
      .site-title-nav.headroom--not-top {
        transform: translateY(0%); } }
  @media (max-width: 599px) {
    .site-title-nav.headroom--unpinned {
      transform: translateY(-100%); } }

.site-title {
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 23px;
  text-align: center;
  margin-bottom: 24px; }
  @media (min-width: 600px) {
    .site-title {
      font-size: 24px;
      line-height: 29px; } }
  .site-title a {
    color: inherit;
    text-decoration: none; }

.button.site-header-button {
  margin-top: 32px; }
  @media (min-width: 600px) {
    .button.site-header-button {
      display: none; }
      .button.site-header-button.is-visible {
        display: inline-block; } }

.site-nav {
  display: none; }
  @media (min-width: 600px) {
    .site-nav {
      display: block;
      text-align: center; } }

@media (min-width: 600px) {
  .site-nav-list {
    display: flex;
    justify-content: space-between; } }

.site-nav-item {
  margin-top: 24px; }
  @media (min-width: 600px) {
    .site-nav-item {
      margin-top: 0; } }

.site-nav-link {
  font-size: 16px;
  letter-spacing: .8px;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase; }
  .site-nav-link:hover {
    text-decoration: underline; }
  @media (min-width: 600px) {
    .site-nav-link {
      font-size: 18px;
      letter-spacing: 1.5px; } }
  .site-nav-link.active {
    font-weight: bold; }

.close-icon {
  display: none; }

.menu-icon {
  display: block; }

.nav-handler {
  display: none; }
  .nav-handler:checked ~ .site-nav {
    display: block; }
  .nav-handler:checked ~ .nav-toggler {
    font-weight: bold; }
  .nav-handler:checked ~ .nav-toggler .close-icon {
    display: block; }
  .nav-handler:checked ~ .nav-toggler .menu-icon {
    display: none; }

.nav-toggler {
  text-align: center;
  font-size: 1rem;
  display: inline-block;
  letter-spacing: .7px;
  text-transform: uppercase; }
  @media (min-width: 600px) {
    .nav-toggler {
      display: none; } }

.nav-toggler-text {
  display: block; }

.site-footer {
  border-top: 8px solid #000;
  padding-top: 24px;
  padding-bottom: 32px; }
  .site-footer p {
    font-size: 14px;
    line-height: 21px; }

.footer-row:after {
  content: '';
  display: block;
  clear: both; }

@media (min-width: 600px) {
  .footer-info {
    float: left;
    margin-right: 160px; } }

@media (min-width: 600px) {
  .footer-social {
    float: left; } }

.social:after {
  content: '';
  display: block;
  clear: both; }

.social li {
  float: left; }
  .social li:not(:last-of-type) {
    margin-right: 1.5rem; }

.button {
  display: inline-block;
  background-color: #97c8ea;
  padding: .5rem 1rem;
  text-transform: uppercase;
  font-weight: bold;
  color: #000;
  text-decoration: none;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 1.2px; }
  .button:hover {
    background-color: #000;
    color: #fff; }

.container {
  max-width: 700px;
  width: 100%;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: auto;
  margin-right: auto; }

.section {
  margin-bottom: 88px; }
  .section:last-of-type {
    margin-bottom: 120px; }
  .section p {
    margin-bottom: 24px; }
  .section h2 {
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 1.2px;
    margin-bottom: 24px;
    margin-top: 48px; }
  .section h3 {
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 1px;
    margin-top: 24px;
    margin-bottom: 4px; }
  .section > * {
    margin-top: 0; }
  .section ul {
    margin-bottom: 24px; }

.section-title {
  text-transform: uppercase;
  font-size: 21px;
  line-height: 24px;
  letter-spacing: 1.5px;
  text-align: center;
  margin-bottom: 56px;
  padding-top: 32px; }

.section-figure {
  margin: 0;
  margin-bottom: 72px;
  margin-left: -30px;
  margin-right: -30px;
  height: 89px; }
  .section-figure img {
    object-fit: cover;
    height: 100%; }
  @media (min-width: 600px) {
    .section-figure {
      margin-left: 0;
      margin-right: 0;
      text-align: center; } }

.section ul {
  padding-left: 24px; }
  @media (min-width: 600px) {
    .section ul {
      padding-left: 80px; } }
  .section ul li {
    margin-bottom: 24px;
    position: relative; }
    .section ul li:before {
      content: '-';
      position: absolute;
      right: calc(100% + 8px);
      top: 2px; }

.section-action {
  margin-top: 72px;
  text-align: center; }
  .section-action.headroom--not-top {
    width: 100%;
    position: fixed;
    top: 137px;
    left: 0; }

.person {
  margin-top: 72px; }

.person-name.person-name {
  margin-bottom: 0; }

.person-figure {
  margin-bottom: 32px; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0; }
