.button {
  display: inline-block;
  background-color: #97c8ea;
  padding: .5rem 1rem;
  text-transform: uppercase;
  font-weight: bold;
  color: $black;
  text-decoration: none;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 1.2px;

  &:hover {
    background-color: $black;
    color: $white;
  }
}
